<template>
  <div class="router-bar-item" @click="changePath(path)">
    <slot name="router-bar-item"></slot>
    <div class="slider" v-if="isActive"></div>
  </div>
</template>

<script>
  export default {
    name: "RouterBarItem",
    props: {
      path: {
        type: String,
        required: true
      }
    },
    methods: {
      changePath() {
        this.$router.replace(this.path)
      }
    },
    computed: {
      isActive() {
        console.log(this.$route.path)
        console.log(this.path)
        return this.$route.path.indexOf(this.path) !== -1
      }
    }
  }
</script>

<style scoped>
  .router-bar-item {
    cursor: pointer;
    width: 120px;
    height: 100%;
    text-decoration: none;
    color: #2ed573;
    font-weight: bold;
    font-size: 18px;
    letter-spacing: 3px;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  .router-bar-item:hover {
    background: #2ed573;
    color: white;
  }
  .slider {
    width: 100px;
    height: 3px;
    background-color: #2ed573;
    border-radius: 4px;
    position: absolute;
    bottom: 0;

    z-index: -1;
  }
</style>
