<template>
  <div class="button-item" @mouseenter="isEnter=true" @mouseleave="isEnter=false"
       :style="{border: '1px solid '+color, backgroundColor:isEnter?color:'#fff'}">
    <slot name="button-item"></slot>
  </div>
</template>

<script>
  export default {
    name: "ButtonItem",
    props: {
      color: {
        type: String,
        default: '#ffffff'
      }
    },
    data(){
      return{
        isEnter: false
      }
    }
  }
</script>

<style scoped>

  .button-item {
    --enterColor: #fff;
    height: 40px;
    width: 100px;
    margin-right: 20px;

    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
    font-size: 11px;
    font-weight: 700;
    border-radius: 20px;
    font-family: Montserrat, Helvetica, sans-serif;
    text-transform: uppercase;
    letter-spacing: 0.065em;
  }

  .button-item:hover {
    color: white;
    background-color: var(--enterColor);
  }
</style>
